<template>
  <ArticleSection :article="article" />
</template>

<script>
import articles from "../assets/articles.json";
import ArticleSection from "../components/ArticleSection";
export default {
  name: "Article",
  components: { ArticleSection },
  data: () => {
    return {
      article: null
    };
  },
  mounted() {
    this.article = this.getArticle(this.$route.params.id);
  },
  methods: {
    getArticle(id) {
      for (let category of articles) {
        for (let article of category.articles) {
          if (article.id == id) {
            return article;
          }
        }
      }
    }
  }
};
</script>

<style scoped></style>
