<template>
  <div class="background-container" :style="getProperStyle()">
    <div class="cms-container">
      <h1 class="cms-title">{{article.title}}</h1>
      <div class="cms-text" v-html="article.body">
      </div>
      <a v-if="article.id == 7" href="./RestituzionePCTO.pdf" target="_blank">Clicca qui per sapere di più</a>
    </div>
  </div>

</template>

<script>
  export default {
    name: "ArticleSection",
    props: {
      article: {
        type: Object,
        required: true
      }
    },
    methods: {
      getImageUrl() {
        return require(`../assets/images/image${this.article.id}.jpg`)
      },
      getProperStyle() {
        return {
          background: `linear-gradient(#00000088, #00000088), url(${this.getImageUrl()})`,
          backgroundPosition: 'center center',
          backgroundAttachment: 'fixed',
          backgroundSize: 'cover'
        }
      }
    }
  }
</script>

<style lang="scss">
  @import "../styles/theme";
  .background-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .cms-container {
    background-color: $lighter;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 3em;
    margin: 5em;

    @media (max-width: 768px) {
      margin: 1em;
    }
    border-radius: 30px;

    .cms-title {
      font-size: 32pt;
      color: $primary;
    }

    .cms-image {
      width: 50vw;
    }
    .cms-text {
      text-align: justify;
      padding-top: 30px;
      font-size: 14pt;

      h1 {
        font-size: 24pt;
        color: #ff5d28;
      }
    }

  }



  .subtitle {
    font-size: 1em;
    color: $primary;
  }
</style>